/* Notion Typography: Segoe UI for Windows, San Francisco for Mac */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  -webkit-font-smoothing: antialiased;
  background-color: #ffffff;
  color: #37352f;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
}

h1 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 40px;
  letter-spacing: -0.01em;
}

.card-container {
  perspective: 1000px;
  width: 400px;
  height: 280px;
  margin-bottom: 30px;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
  cursor: pointer;
}

.card-inner.flipped {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  box-sizing: border-box;
  border-radius: 8px;
  border: 1px solid rgba(55, 53, 47, 0.09);
  box-shadow: rgba(15, 15, 15, 0.05) 0px 1px 2px;
  font-size: 18px;
  line-height: 1.5;
  text-align: center;
}

.card-front {
  background: white;
}

.card-back {
  background: #f7f6f3; /* Notion's light gray background */
  transform: rotateY(180deg);
}

/* Notion-style Button */
.next-btn {
  background: white;
  border: 1px solid rgba(55, 53, 47, 0.16);
  color: #37352f;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s ease;
}

.next-btn:hover {
  background: rgba(55, 53, 47, 0.08);
}

.button-group {
  display: flex;
  gap: 12px;
  margin-top: 20px;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.study-btn {
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid rgba(55, 53, 47, 0.16);
  font-weight: 600;
  cursor: pointer;
  background: white;
  transition: all 0.2s ease;
}

.study-btn.again { color: #eb5757; } /* Red */
.study-btn.hard { color: #d9730d; }  /* Orange */
.study-btn.good { color: #2eaadc; }  /* Blue */
.study-btn.easy { color: #0f7b6c; }  /* Green */

.study-btn:hover {
  background: #f1f1f1;
  transform: translateY(-2px);
}

.hint {
  color: #adaba7;
  font-size: 14px;
  margin-top: 20px;
}